import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';
import producers from '@/utils/producers.js';

import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import uniCalendar from '@/components/uni-calendar/uni-calendar.vue';
import producerDetBottom from '@/components/producerDetBottom.vue'

let that;
let uid;
export default {
	components: {
		producerDetBottom,
		uniCalendar,
		uniSegmentedControl
	},

	data() {
		return {
			items: ['采购情况', '还款记录'],
			current: 0,
			sellType: 0,

			producer: {},
			sellParams: {
				producerId: '',
				startDate: common.getDay(0, false),
				endDate: common.getDay(1, false),
				type: 1,
				extra_type: 1,
			}, //获取采购情况参数
			buyTotal: {},//采购情况记录
			getMoney:{},//还款情况记录
		}
	},

	onLoad(options) {
		that = this;
		that.sellParams.producerId = options.id;
		uid = uni.getStorageSync('uid');
		//console.log(options.id)
	},
	
	onShow() {
		producers.producer_detail(that.sellParams.producerId).then(res => {
			let producer = res
			const query = Bmob.Query("order_opreations");
			query.equalTo("type", "==", 1);
			query.equalTo("extra_type", "==", 1);
			query.equalTo("master", "==", uid);
			query.equalTo("producer", "==", that.sellParams.producerId);
			query.statTo("sum", "debt");
			query.find().then(res => {
				if(res.length > 0){
					producer.debt = res[0]._sumDebt
				}else{
					producer.debt = 0
				}
				that.producer = producer
			})
			
			that.sellParams.producerId = res.objectId
			if(that.current == 0){
				producers.getAllProducerSellList(that.sellParams).then(res => {
					that.buyTotal = res
				})
			}else if(that.current == 1){
				producers.getAllProducerInRecord(that.sellParams).then(res => {
					that.getMoney = res
				})
			}
		})
	},
	
	methods: {

		showOptions() {
			uni.showActionSheet({
				itemList: ['采购', '退货'],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.sellType = 0
						that.sellParams.type = 1
						that.sellParams.extra_type = 1
					} else {
						that.sellType = 1
						that.sellParams.type = -1
						that.sellParams.extra_type = 4
					}
					producers.getAllProducerSellList(that.sellParams).then(res => {
						that.buyTotal = res
					})
				}
			});
		},

		open() {
			that.$refs.calendar.open();
		},

		confirm(e) {
			console.log(e)
			if (e.range.data.length == 0) {
				that.sellParams.startDate = e.fulldate
			} else {
				that.sellParams.startDate = e.range.data[0];
				that.sellParams.endDate = e.range.data[e.range.data.length - 1];
			}
			if(that.current == 0){
				producers.getAllProducerSellList(that.sellParams).then(res => {
					console.log(res)
					that.buyTotal = res
				})
			}else if(value == 1){
				producers.getAllProducerInRecord(that.sellParams).then(res => {
					console.log(res)
					that.getMoney = res
				})
			}
		},

		onClickItem(value) {
			that.current = value
			if(that.current == 0){
				producers.getAllProducerSellList(that.sellParams).then(res => {
					console.log(res)
					that.buyTotal = res
				})
			}else if(that.current == 1){
				producers.getAllProducerInRecord(that.sellParams).then(res => {
					console.log(res)
					that.getMoney = res
				})
			}
		},

		//打电话
		makePhoneCall(phone) {
			if (phone) {
				uni.makePhoneCall({
					phoneNumber: phone //仅为示例
				});
			} else {
				uni.showToast({
					icon: "none",
					title: "未填写联系电话"
				})
			}
		},

		show_options() {
			uni.showActionSheet({
				itemList: ["编辑", "删除"],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.edit(that.producer)
					} else if (res.tapIndex == 1) {
						that.delete_this(that.producer.objectId)
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//编辑操作
		edit(item) {
			uni.setStorageSync("producers", item);
			uni.setStorageSync("producer_type", "producers");
			uni.navigateTo({
				url: "../add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此客户',
				success: function(res) {
					if (res.confirm) {
						that.delete_data("producers", id)
					}
				}
			});
		},

		//删除数据
		delete_data(type, id) {
			console.log(id)
			const query = Bmob.Query(type);
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				uni.navigateBack({
					delta: 1
				})
			}).catch(err => {
				console.log(err)
			})
		},

	},
}
